.accordionList {
  font-family: Raleway;
  display: flex;
  flex-direction: column;
  position: relative;
}
@media only screen and (max-width: 560px) {
  .accordionList {
    margin-top: 3.5em;
  }
}

.accordionItem {
  position: static;
  display: flex;
  width: 100%;
  align-items: center;
  column-gap: 8.2em;
}
@media only screen and (max-width: 880px) {
  .accordionItem {
    flex-direction: column;
    position: relative;
    max-width: 100%;
    margin: 0 auto;
  }
}

.itemTitle {
  color: #040404;
  font-size: 2em;
  border-bottom: 1px solid #bebcdc;
  max-width: 465px;
  width: 100%;
  display: flex;
  padding: 1em 0;
  position: relative;
  background-position: center left;
  transition: all 0.3s linear;
  cursor: pointer;
}
@media only screen and (max-width: 880px) {
  .itemTitle {
    max-width: 100%;
  }
}
@media only screen and (max-width: 560px) {
  .itemTitle {
    font-size: 1.75em;
  }
}

.activeTitle {
  background-repeat: no-repeat;
  background-position: center right;
  background-size: auto 24px;
}

.descriptionBlockWrap {
  display: flex;
  max-width: 564px;
  width: 100%;
  min-width: 440px;
  transition: all 0.3s linear;
}
@media only screen and (max-width: 880px) {
  .descriptionBlockWrap {
    margin-top: 0;
    min-width: 0;
    height: 0;
    opacity: 0;
  }
}

.descriptionBlock {
  position: absolute;
  display: none;
  flex-direction: column;
  top: 50%;
  transform: translateY(-50%);
  background-color: #ffffff;
  transition: all 0.3s linear;
  box-shadow: 7px 9px 8px rgba(32, 3, 78, 0.07);
  border: 1px solid #482c42;
  border-radius: 8px;
  padding: 2em;
  height: 345px;
}
@media only screen and (max-width: 880px) {
  .descriptionBlock {
    position: relative;
    transform: translateY(0);
  }
}
.descriptionBlock span {
  font-size: 1.5em;
  padding: 0;
  font-weight: 400;
  color: #482c42;
  margin: auto 0;
  line-height: 140%;
}
@media only screen and (max-width: 560px) {
  .descriptionBlock span {
    font-size: 1em;
  }
}
.descriptionBlock span p {
  margin-top: 1em;
}
@media only screen and (max-width: 360px) {
  .descriptionBlock span p {
    margin-top: 0;
  }
}

.choosedItem .itemTitle {
  color: #25025d;
  font-weight: 700;
  border-bottom: 2px solid #25025d;
}
@media only screen and (max-width: 880px) {
  .choosedItem .descriptionBlockWrap {
    max-width: 100%;
    margin-top: 2.1em;
    height: 260px;
    opacity: 1;
  }
}
.choosedItem .descriptionBlock {
  display: flex;
}
@media only screen and (max-width: 880px) {
  .choosedItem .descriptionBlock {
    width: 100%;
    padding: 1.5em;
    height: 100%;
    top: 0;
  }
}

.buttonComponent {
  max-width: 248px;
  margin: 2.2em auto 0;
  width: 100%;
}
@media only screen and (max-width: 360px) {
  .buttonComponent {
    margin: 1em auto 0;
  }
}